import { GameManager } from './game/GameManager';
import './styles.css';

/**
 * CoinSlosh Entry Point
 */

console.log('üì¶ main.ts loaded!');

// Wait for DOM to be ready
window.addEventListener('DOMContentLoaded', async () => {
  console.log('üé∞ CoinSlosh Starting...');
  console.log('üåê DOM loaded');

  // Get canvas element
  const canvas = document.getElementById('gameCanvas') as HTMLCanvasElement;
  if (!canvas) {
    console.error('‚ùå Canvas element not found!');
    return;
  }
  console.log('‚úÖ Canvas found:', canvas);

  // Check WebGL support
  const gl = canvas.getContext('webgl2') || canvas.getContext('webgl');
  if (!gl) {
    alert('WebGL not supported. Please use a modern browser.');
    console.error('‚ùå WebGL not supported');
    return;
  }
  console.log('‚úÖ WebGL supported');

  try {
    // Create and initialize game
    console.log('üéÆ Creating GameManager...');
    const game = new GameManager();
    
    console.log('üîß Initializing game...');
    await game.initialize(canvas);

    // Expose game instance for debugging
    (window as any).game = game;

    console.log('üéÆ CoinSlosh ready! Click drop slots to play.');
  } catch (error) {
    console.error('‚ùå Failed to start game:', error);
    console.error('Stack:', error instanceof Error ? error.stack : 'No stack');
    alert('Failed to initialize game. Please refresh and try again.');
  }
});

// Handle page visibility changes (pause when hidden)
document.addEventListener('visibilitychange', () => {
  if (document.hidden) {
    console.log('‚è∏Ô∏è  Game paused (tab hidden)');
  } else {
    console.log('‚ñ∂Ô∏è  Game resumed');
  }
});

// Handle beforeunload (save state)
window.addEventListener('beforeunload', () => {
  console.log('üíæ Saving game state...');
  // State is auto-saved on changes, but this is a safety net
});
